@property --d-cssvar-font-variation-settings-fill {
  syntax: '<number>';
  inherits: false;
  initial-value: 0;
}

span.css_material_symbols_rounded {
  font-family: 'Material Symbols Rounded';
  font-weight: normal;
  font-style: normal;
  font-size: 24px;
  font-variation-settings:
    'FILL' var(--d-cssvar-font-variation-settings-fill),
    'wght' 300,
    'GRAD' 0,
    'opsz' 24;
  line-height: 1;
  letter-spacing: normal;
  text-transform: none;
  white-space: nowrap;
  word-wrap: normal;
  direction: ltr;
  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: antialiased;
  user-select: none;
  -webkit-user-select: none;
  position: relative;
  width: 20px;
  height: 20px;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
}