.css_library_field .css_library_head {
  position: absolute;
  top: 0px;
  left: 0px;
  width: 100%;
  height: 55px;
  box-sizing: border-box;
  background: var(--d-cssvar-ffffff);
}

.css_library_field .css_library_head .css_library_button_left,
.css_library_field .css_library_head .css_library_button_right {
  width: 55px;
  height: 55px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.css_library_field .css_library_head .css_library_button_left {
  position: absolute;
  top: 0px;
  left: 0px;
}

.css_library_field .css_library_head .css_library_button_right {
  position: absolute;
  top: 0px;
  right: 0px;
}

.css_library_field .css_library_head .css_library_button_left span.css_material_symbols_rounded,
.css_library_field .css_library_head .css_library_button_right span.css_material_symbols_rounded {
  height: 20px;
  width: 20px;
  color: var(--d-cssvar-000408);
}

.css_library_field .css_library_head .css_library_title {
  position: absolute;
  top: 0px;
  left: 55px;
  width: calc(100% - 110px);
  height: 55px;
  color: var(--d-cssvar-000408);
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 20px;
  font-weight: 500;
  user-select: none;
  -webkit-user-select: none;
}